import React, { Component } from 'react';
import Header from '../../components/header/Header';
import Footer from '../../components/footer/Footer';
import GithubRepoCard from '../../components/githubRepoCard/GithubRepoCard';
import Button from "../../components/button/Button";
import {Fade} from 'react-reveal';
import ProjectsData from '../../shared/opensource/projects.json';
import './Projects.css';

const projectsHeader = {
    title: "Projects",
    description: "My hobby and personal projects range across wide spectrum of technologies in Computer Science. I work on interesting ideas whenever I find time. Most of the projects I do are foucsed on developer tools.",
    avatar_image_path: "projects_image.svg"
}


class Projects extends Component {

	constructor(props) {
		super(props)
		this.state = {
			projects : []
		}
	}

	componentDidMount() {
		//Get projects
		fetch('https://api.github.com/users/Narasimha1997/repos?per_page=100', {
			method : 'GET'
		}).then((response) => response.json()).then((projects) => {
			//sort according to starts and remove the starred repos
			projects = projects.filter((project) => !project.fork).sort((p1, p2) => p1.stargazers_count < p2.stargazers_count)
			this.setState({projects : projects})
		})
	}

    render(){
        return (
					<div className="projects-main">
						<Header />
						<div className="basic-projects">
							<Fade bottom duration={1000} distance="40px">
								<div className="projects-heading-div">
									<div className="projects-heading-img-div">
										<img
											src={require(`../../assests/images/${projectsHeader["avatar_image_path"]}`)}
											alt=""
										/>
									</div>
									<div className="projects-heading-text-div">
										<h1 className="projects-heading-text">
											{projectsHeader.title}
										</h1>
										<p className="projects-header-detail-text subTitle">
											{projectsHeader["description"]}
										</p>
									</div>
								</div>
							</Fade>
						</div>
						<div className="repo-cards-div-main">
							{this.state.projects.map(repo => {
								return <GithubRepoCard repo={repo} />;
							})}
						</div>
						<Button
							text={"See on GitHub"}
							className="project-button"
							href="https://github.com/Narasimha1997/"
							newTab={true}
						/>
						<Footer />
					</div>
				);
    }
}

export default Projects;